:root {
    box-sizing: border-box;
    font-size: calc(0.5em + 1vw);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    /* Variables */
    --background-color-main: #39393A;
    --color-main: var(--background-color-main);
    --color-title: #FF8552;
    --color-subTitle: #85FFC7;
    --container-background-color: #E6E6E6;
}

* {
    margin: 0;
    padding: 0;
    overflow: auto;
}

body * + * {
    margin-top: 1.5em;
}

body {
    box-sizing: inherit;

    background-color: var(--background-color-main);
    color: var(--color-main);
}

p {
    font-weight: 350;
}

a {
    text-decoration: underline;
    text-decoration-color: var(--color-subTitle);
    text-decoration-thickness: 7%;
}

a:hover {
    text-decoration: none;
}

.title-center {
    color: var(--color-title);
    text-align: center;
    margin-top: -0.25em;
    font-size: 2.6em;
}

.subTitle-center {
    color: var(--color-subTitle);
    text-align: center;
    margin-top: .1em;
    font-size: .9em;
    letter-spacing: .05em;
}

#container {
    background-color: var(--container-background-color);
    display: block;
    width: 75vw;

    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1em;

    padding: 1.4em;
    border-radius: 10px;
    min-height: 30vh;
}

.inputs {
    font-size: .75em;
    padding: .8em;
    border-radius: 50px;

    display: block;
    margin-left: auto;
    margin-right: auto;

    margin-top: .5em;
    margin-bottom: 2em;
    
    width: 55vw;
    text-align-last: center;
    transition: 0.25s;
}

.inputs, .button {
    outline: 0;
    border: 0.15px solid var(--background-color-main);
}

.button {
    cursor: pointer;

    background-color: var(--background-color-main);
    color: white;

    border-radius: 5px;
    text-align: center;

    display: block;
    margin-left: auto;
    margin-right: auto;

    width: 10em;
    min-height: 2.5em;
    font-size: .9em;

    text-decoration: underline;
    text-decoration-color: var(--color-subTitle);
}

.button:hover {
    text-decoration: none;
}

.inputs:hover {
    background-color: var(--background-color-main);
    color: white;

    transform: scale(1.1);
}

input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

@media only screen and (max-width: 768px) {
    .inputs, .button {
        font-size: 1.20em;
    }
}

.final-result, .final-result-description {
    text-align: center;
}

.final-result-description {
    margin-top: .5em;
}

#go-back-arrow {
    width: 1.25em;
    position: relative;
    bottom: 3.1em;
    cursor: pointer;
}

.question {
    text-align: center;
}
